import React, { Component } from 'react'
import MoviesItem from './MoviesItem'

export class Movie extends Component {

    constructor() {
        super();
        this.state = {
            Search: [],
            loading: false
        }
    }

    async componentDidMount() {
        let url = "https://www.omdbapi.com/?s=Hollywood&apikey=5e8dc8bc";
        let data = await fetch(url);
        let parsedData = await data.json()
        this.setState({ Search: parsedData.Search })
    }

    render() {
        return (
            <div className="container my-3 justify-content-center">
                <h1 className="m-5 text-center">NETFLIX</h1>
                <div className="row">
                    {this.state.Search.map((element) => {
                        return <div className="col-md-4" key={element.Type}>
                            <MoviesItem Title={element.Title} Year={element.Year} Poster={element.Poster} />
                        </div>
                    })}
                </div>
            </div>
        )
    }
}

export default Movie
